@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
body{margin:0;font-family:'Roboto Slab', serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.tile-timeline{display:flex;flex-direction:column;padding:4px 0;min-height:800px;background-color:#F7AF1F}.tile-timeline .dot{height:5px;width:5px;background-color:#fff;border-radius:50%;display:inline-block;margin-right:10px}.tile-timeline .wrapper{display:flex;flex-direction:row;flex:1 1}.tile-timeline h2{border-bottom:4px solid #fff;margin:10px 0}.tile-timeline h2.title{border-bottom:0;text-align:center}.tile-timeline .vertical-line{border-left:4px dotted white}.tile-timeline .project-wrapper{margin:0 25px 25px;border-bottom:1px solid;padding-bottom:25px}.tile-timeline .project-wrapper i{display:block}.tile-timeline .project-wrapper:last-child{border-bottom:0}.tile-timeline .top{padding:25px 0 25px;text-align:right;width:50%}.tile-timeline .top h2::after{content:'';display:inline-block;width:12px;height:12px;border-radius:50%;background-color:#fff;margin-bottom:-16px;margin-right:-8px}.tile-timeline .top p,.tile-timeline .top h4,.tile-timeline .top i{margin:10px 0;padding:0 20px}.tile-timeline .bottom{display:flex;flex-direction:column;justify-content:flex-end;padding:25px 0 25px;text-align:left;width:49%}.tile-timeline .bottom h2::before{content:'';display:inline-block;width:12px;height:12px;border-radius:50%;background-color:#fff;margin-bottom:-15px;margin-left:-8px}.tile-timeline .bottom p,.tile-timeline .bottom h4,.tile-timeline .bottom i{margin:10px 0;padding:0 20px}

.tile-profile{padding:25px;text-align:center;background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(255,0,0,0)), to(#e94e36));background-image:linear-gradient(to bottom, rgba(255,0,0,0), #e94e36)}.tile-profile .avatar{height:150px;width:150px;border-radius:50%}.tile-profile h1{text-transform:uppercase;letter-spacing:3px}.tile-profile .dot{height:4px;width:4px;background-color:#fff;border-radius:50%;display:inline-block;margin-right:10px}

.tile-footer{background-color:gray}

.app-container{width:60%;margin:auto}.app-container .tile{color:#fff;margin:25px;border-radius:5px}.app-container a{color:#fff;text-decoration:none}.app-container a:hover{text-decoration:underline}

